"use client";

import Header from './components/Header';
import Card from './components/Card';
import { useEffect, useState } from 'react';
import axios from 'axios';
const Spotify = require("spotify-api.js");
const { Client } = require("spotify-api.js");

export default function Home() {
  const [artists, setArtists] = useState([]);

  async function fetchMusic() {
    const res = await fetch('http://localhost:8000/artists', {
      method: 'GET',
    });

    if (res.status !== 200) {
      throw new Error('Failed to authenticate with API');
    }
    const data = await res.json();
    setArtists(data?.artists);
  }

  useEffect(() => {
    fetchMusic();
  }, []);

  return (
    <div className="bg-black min-h-screen text-white">
      <div className="flex">
        {/* Sidebar */}
        <aside className="w-64 bg-black p-6 hidden md:block flex-shrink-0">
          <div className="font-bold text-2xl mb-10">Spotify</div>
          <nav>
            <ul>
              <li className="mb-4">
                <a href="#" className="flex items-center text-zinc-400 hover:text-white transition-colors duration-300">
                  <svg className="w-6 h-6 mr-3" fill="currentColor" viewBox="0 0 20 20"><path d="M10.707 2.293a1 1 0 00-1.414 0l-7 7a1 1 0 001.414 1.414L4 10.414V17a1 1 0 001 1h2a1 1 0 001-1v-2a1 1 0 011-1h2a1 1 0 011 1v2a1 1 0 001 1h2a1 1 0 001-1v-6.586l.293.293a1 1 0 001.414-1.414l-7-7z"></path></svg>
                  Home
                </a>
              </li>
              <li className="mb-4">
                <a href="#" className="flex items-center text-zinc-400 hover:text-white transition-colors duration-300">
                  <svg className="w-6 h-6 mr-3" fill="none" stroke="currentColor" viewBox="0 0 24 24"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"></path></svg>
                  Search
                </a>
              </li>
              <li>
                <a href="#" className="flex items-center text-zinc-400 hover:text-white transition-colors duration-300">
                  <svg className="w-6 h-6 mr-3" fill="none" stroke="currentColor" viewBox="0 0 24 24"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M9 19V6l12-3v13M9 19c0 1.105-1.343 2-3 2s-3-.895-3-2 1.343-2 3-2 3 .895 3 2zm12-13c0 1.105-1.343 2-3 2s-3-.895-3-2 1.343-2 3-2 3 .895 3 2zM9 6l12 3"></path></svg>
                  Your Library
                </a>
              </li>
            </ul>
          </nav>
        </aside>

        {/* Main Content */}
        <main className="flex-1">
          <div className="bg-gradient-to-b from-[#1f1f1f] to-[#121212] p-6 rounded-t-lg">
            <div className="mt-10">
              <h2 className="text-3xl font-bold mb-5">Good Afternoon</h2>
              <div className="grid grid-cols-2 sm:grid-cols-3 lg:grid-cols-4 xl:grid-cols-6 gap-4">
                {
                  artists?.map((artist) => {
                    return (
                      <Card
                        key={artist?._id}
                        artist={artist}
                      />
                    )
                  })
                }
              </div>
            </div>
            <div className="mt-10">
              <h2 className="text-3xl font-bold mb-5">Your Top Mixes</h2>
              <div className="grid grid-cols-2 sm:grid-cols-3 lg:grid-cols-4 xl:grid-cols-6 gap-4">
                <Card
                  image="https://placehold.co/150x150/777777/FFFFFF?text=Mix+1"
                  title="My Mix 1"
                  description="A blend of pop and rock."
                />
                <Card
                  image="https://placehold.co/150x150/555555/FFFFFF?text=Mix+2"
                  title="My Mix 2"
                  description="Chill electronic beats."
                />
                <Card
                  image="https://placehold.co/150x150/333333/FFFFFF?text=Mix+3"
                  title="My Mix 3"
                  description="Indie and alternative."
                />
                <Card
                  image="https://placehold.co/150x150/999999/FFFFFF?text=Mix+4"
                  title="My Mix 4"
                  description="Focus and study music."
                />
              </div>
            </div>
          </div>
        </main>
      </div>
    </div>
  );
}
